import {Injectable} from '@angular/core';

@Injectable({
    providedIn: 'root'
})
export class GenericService {


    private scrollHandlers: {
        [key: string]: () => void;
    } = {};

    private scrollHandler() {
        //console.log(this.scrollHandlers);
        if(!this.scrollHandlers) {
            return;
        }
        let oKeys = Object.keys(this.scrollHandlers);

        for (let i = 0; i < oKeys.length; i++) {
            let tmpFunc = this.scrollHandlers[oKeys[i]];
            if (tmpFunc) {
                tmpFunc();
            }
        }
    }

    public addScrollHandler(pKey: string, pFunc: () => void): void {
        this.scrollHandlers[pKey] = pFunc;
    }

    public removeScrollHandler(pKey: string): void {
        this.scrollHandlers[pKey] = undefined;
    }

    constructor() {
        window.addEventListener('scroll', () => {this.scrollHandler()});
    }
}
