import {Component, OnInit, Input, AfterViewInit, ViewChild, ElementRef} from '@angular/core';
import {IBasicSection} from "@commonLib/interfaces/components"
import {GenericService} from '../../services/generic.service';

@Component({
    selector: 'app-splash',
    templateUrl: './splash.component.html',
    styleUrls: ['./splash.component.scss']
})
export class SplashComponent implements OnInit, AfterViewInit, IBasicSection {

    @Input() anchorId: string = 'splash';
    @Input() nextSectionId: string = '';
    @ViewChild('nextSectionComponent') nextSectionComponent: ElementRef;
    timeoutHandler: any;

    constructor(private genericService: GenericService) {}

    ngOnInit(): void {
    }

    ngAfterViewInit() {
        this.timeoutHandler = setTimeout(() => {
            this.nextSectionComponent.nativeElement.click();
        }, 2000);

        this.genericService.addScrollHandler('splash', () => {
            clearTimeout(this.timeoutHandler);
            this.genericService.removeScrollHandler('splash');
        })
    }

    disableTimeoutHandler(e: Event) {
        if (this.timeoutHandler) {
            clearTimeout(this.timeoutHandler);
        }
    }
}
